import React, { Component } from "react";
import CustomerTable from "./invoice_customerTable";
import Random from "./invoice_random";
import ClientData from "./MOCK_DATA";

class Invoice extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
      clients: ClientData,
      iteration: 100
    };
    this.createData = this.createData.bind(this);
    this.sortData = this.sortData.bind(this);
  }

  createData() {
    const { iteration } = this.state;
    const { invoices, invoiceDate, cost, phoneNumber, number } = Random;
    let counter = 0;
    let arr = [];

    while (arr.length !== iteration) {
      ++counter;
      let date = invoiceDate();
      let price = cost();
      let invoiceCount = invoices();
      let collection = [];
      let client = this.state.clients[number(0, this.state.clients.length)];

      const transaction = [
        counter, // id
        invoiceCount, //IvoiceNum
        date
      ];
      const invoice = {
        date: date, //InvoiceDate
        paid: price, //InvoiceAmount
        due: price //InvoiceDue
      };

      while (collection.length < invoiceCount) {
        collection.push(invoice);
      }

      const info = [
        client.company, // name
        client.email, //email
        phoneNumber(), //Phone Number
        client.billing, // Billing Address
        client.shipping // Shipping Address
      ];

      arr.push(CustomerTable(...info, ...transaction, ...collection));
    }
    this.setState({ data: [...arr] });
  }

  sortData() {
    const randomData = [...this.state.data];

    const compare = (a, b) => {
      const itemA = Date.parse(a.transactions.invoices[0].date);
      const itemB = Date.parse(b.transactions.invoices[0].date);
      // const itemA = Date.parse(a.transactions.lastDate);
      // const itemB = Date.parse(b.transactions.lastDate);
      if (itemA < itemB) {
        return 1;
      } else if (itemA > itemB) {
        return -1;
      } else {
        return 0;
      }
    };
    this.setState({ data: [...randomData.sort(compare)] });
  }

  componentDidMount() {
    this.createData();
  }

  render() {
    let upTo = 15;
    let count = 1;

    const topInvoices = this.state.data.map(item => {
      let totalCost = 0;
      item.transactions.invoices.map(invoice => (totalCost += invoice.paid));
      while (count <= upTo) {
        ++count;
        return (
          <div key={item.name + item.phone}>
            <h3>{item.name}</h3>
            <ul>
              <li>Phone: {item.phone}</li>
              <li>Email: {item.email}</li>
              <li>Total Spent ${totalCost}</li>
              <li>Total Invoices {item.transactions.invoiceNum}</li>
              <li>Last Payment Received: {item.transactions.lastDate}</li>
            </ul>
          </div>
        );
      }
      return ""; // return statement to appease the linter
    });
    return (
      <div className="module module_invoice">
        <h1>Invoice data</h1>

        <i>
          An operation for finding the most recent invoice date from a list of
          300 clients. All have varying amounts of invoices between 1-10,
          spanning over a period of 2 years.
        </i>
        <p>
          If you are curious about the attributes of these tables, they are as
          follows: Customer Table: "customer id" "company name" "contact name"
          "contact email" "contact phone" "billing address" "shipping address"
          Transaction Table: "customer id" "invoice number" "invoice date"
          "invoice due date" "invoice amount" "billing terms"
        </p>
        <button onClick={() => this.sortData()}>Sort By Date</button>
        <button onClick={() => this.createData()}>Generate Data</button>
        {topInvoices}
      </div>
    );
  }
}

export default Invoice;

/**
 *
 * Sort invoices, .lastDate = latest one || .transactions.invoices[0].date
 *
 *
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Date/parse
 *
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/random#Getting_a_random_number_between_two_values
 *
 * https://www.w3schools.com/jsref/jsref_sort.asp
 *
 *
 */
