import countryCodes from "./invoice_countryCodes.js";

const random = {
  number: (min, max) => {
    return Math.floor(Math.random() * Math.floor(max - min) + min);
  },
  invoices: function() {
    return random.number(1, 10);
  },

  invoiceDate: () => {
    const randomDay = random.number(1, 31);
    const randomMonth = random.number(1, 12);
    const randomYear = random.number(
      new Date().getFullYear() - 2,
      new Date().getFullYear()
    );

    return `${randomMonth}/${randomDay}/${randomYear}`;
  },

  phoneNumber: () => {
    let number = random.number(1000000, 9999999);
    let index = random.number(0, countryCodes.countries.length);
    let country = countryCodes.countries[index].code;
    let area = random.number(100, 999);

    return `${country} (${area})${number}`;
  },

  cost: () => random.number(1, 999)
};

export default random;
