import React, { Component } from "react";

class Elevator extends Component {
  constructor() {
    super();
    this.state = {
      currentFloor: 3,
      pendingFloors: [],
      direction: [],
      hasStarted: false
    };
    this.handleButtons = this.handleButtons.bind(this);
    this.startElevator = this.startElevator.bind(this);
  }

  handleButtons(event) {
    const { pendingFloors, currentFloor } = this.state;
    let pending = [...pendingFloors];
    let floor = parseInt(event.target.value);

    if (pendingFloors.indexOf(floor) < 0 && currentFloor !== floor) {
      pending.push(floor);
      this.setState({ pendingFloors: [...pending] });
    }
  }

  startElevator(event) {
    event.preventDefault();

    const { currentFloor, pendingFloors } = this.state;
    const goingUp = () => this.state.currentFloor < this.state.pendingFloors[0];
    const goingDown = () =>
      this.state.currentFloor > this.state.pendingFloors[0];
    let sorted = [];
    let endDirection = [];

    if (goingUp()) {
      sorted = [...pendingFloors].sort((a, b) => a - b);
      endDirection = sorted.filter(dest => dest < currentFloor);
      sorted = sorted.filter(dest => dest > currentFloor);
      sorted.push(...endDirection.sort((a, b) => b - a));
    } else if (goingDown()) {
      sorted = [...pendingFloors].sort((a, b) => b - a);
      endDirection = sorted.filter(dest => dest > currentFloor);
      sorted = sorted.filter(dest => dest < currentFloor);
      sorted.push(...endDirection.sort((a, b) => a - b));
    }
    this.setState({ direction: [...sorted] });
  }

  move() {
    const { direction, pendingFloors } = this.state;
    direction.map(step => {
      const momentum = setTimeout(() => {
        if (direction.length - 1 > 0 && direction !== undefined) {
          let passed = arr => this.state[arr.shift()];
          return this.setState({
            currentFloor: step,
            pendingFloors: [passed(pendingFloors)],
            direction: [passed(direction)]
          });
        } else if (step !== undefined && pendingFloors.length >= 1) {
          this.setState({
            currentFloor: step,
            direction: [],
            pendingFloors: []
          });
        } else {
          return clearInterval(momentum);
        }
      }, 2000);

      return step;
    });
  }

  componentDidUpdate(prevProps) {
    if (
      this.state.direction !== prevProps.direction &&
      this.state.direction.length > 0
    ) {
      this.move();
    }
  }

  render() {
    const { pendingFloors, direction, currentFloor } = this.state;
    const path = direction.map((dir, index) => {
      if (direction.length - index - 1 !== 0) {
        return `${dir} ->`;
      } else {
        return `${dir}`;
      }
    });
    return (
      <div id="elevator" className="module module_elevator">
        <h3>Elevator</h3>
        <i>
          Write us an operation for an elevator in a 5 story building with the
          most efficient operation path.
        </i>
        <h5>Current Floor: {this.state.currentFloor}</h5>
        <h5>Pending Floors: {pendingFloors}</h5>
        <h5>Path: {path}</h5>
        <div id="panel">
          <img
            src="https://smhttp-ssl-42830.nexcesscdn.net/media/catalog/product/cache/1/image/500x500/d5a15e872dcc8fe615d4034ffc29fbff/e/l/elevator-control-panel.jpg"
            alt="panel"
          />
          <form id="buttons" onSubmit={event => this.startElevator(event)}>
            <div>
              <label htmlFor="floor5">Floor 5</label>
              <input
                type="checkbox"
                id="floor5"
                name="panelButton"
                value="5"
                checked={pendingFloors.indexOf(5) > -1 || currentFloor === 5}
                onChange={this.handleButtons}
              />
            </div>
            <div>
              <label htmlFor="floor4">Floor 4</label>
              <input
                type="checkbox"
                id="floor4"
                name="panelButton"
                value="4"
                checked={pendingFloors.indexOf(4) > -1 || currentFloor === 4}
                onChange={this.handleButtons}
              />
            </div>
            <div>
              <label htmlFor="floor3">Floor 3</label>
              <input
                type="checkbox"
                id="floor3"
                name="panelButton"
                value="3"
                checked={pendingFloors.indexOf(3) > -1 || currentFloor === 3}
                onChange={this.handleButtons}
              />
            </div>
            <div>
              <label htmlFor="floor2">Floor 2</label>
              <input
                type="checkbox"
                id="floor2"
                name="panelButton"
                value="2"
                checked={pendingFloors.indexOf(2) > -1 || currentFloor === 2}
                onChange={this.handleButtons}
              />
            </div>
            <div>
              <label htmlFor="floor1">Floor 1</label>
              <input
                type="checkbox"
                id="floor1"
                name="panelButton"
                checked={pendingFloors.indexOf(1) > -1 || currentFloor === 1}
                value="1"
                onChange={this.handleButtons}
              />
            </div>

            <button>Start Elevator</button>
          </form>
        </div>
      </div>
    );
  }
}

export default Elevator;
/*

You asked for me to create an Elevator application that chooses the most effective routes
I used real ones as inspiration, the initial button is what determines the direction of the elevator
Then if any floors under the initial were pressed, push them at the end of the route
while also reverse sorting them, so its still effective

Sources that helped me below...

https://developer.mozilla.org/en-US/docs/web/javascript/reference/global_objects/array/filter
https://react.tips/radio-buttons-in-react-16/

https://developer.mozilla.org/en-US/
https://www.w3schools.com/jsref/jsref_obj_array.asp


Definately needs refactoring as I've spent some time patching out potential memory leaks
Most of my time went towards attempting to simulate an elevator with wait times in between each floor...
However it just buckled up

*/
