let CustomerTable = (
  name,
  email,
  phone,
  billing,
  shipping,
  id,
  invoiceNum,
  lastDate,
  ...invoice
) => {
  return {
    name,
    email,
    phone,
    billing,
    shipping,
    transactions: {
      id,
      invoiceNum,
      lastDate,
      invoices: [...invoice]
    }
  };
};

export default CustomerTable;
// do not change order of parameters, will break generator
